import request from '@/utils/request';

export function fetchArticleSummary(query) {
  return request({
    url: '/apis/article-summary.json',
    method: 'get',
    params: query,
  });
}

export function fetchArticleList(id) {
  return request({
    url: '/apis/article-list',
    method: 'get',
    params: { id },
  });
}

export function fetchArticleDetail(name) {
  return request({
    url: '/articles/' + name + '.md',
    method: 'get',
  });
}

export function createArticle(data) {
  return request({
    url: '/article/create',
    method: 'post',
    data,
  });
}



