<template>
  <div class="work-content">
    <div class="work-content-wrap work-content-style">
      <div class="work-modules">
        <!-- This module can be generated by rich editor -->
        <!-- place holder 1400 x 1 px -->
        <div class="module-spacer">
          <img src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAABXgAAAABCAYAAABkHsaZAAAAHElEQVR42u3BAQ0AAADCoPdPbQ43oAAAAAAAuDQV4QABsr+jcAAAAABJRU5ErkJggg==" />
        </div>
        <!--<div class="work-module full" v-for="(token, k) in tokens" :key="k">
          <image-text v-if="token.type === 'image'" />
          <text-text class-name="code-text" v-else-if="token.type === 'code'">
            <editor :code="token.text" :lang="token.lang" />
          </text-text>
          <text-text class-name="main-text" v-else>
            {{ token.text }}
          </text-text>
        </div>-->
        <div class="work-module full">
          <text-image :link="intro" ></text-image>
        </div>
        <div class="spacer">
          <div class="divider"></div>
        </div>
        <div class="work-module markdown" v-highlight v-html="htmlStr" ></div>
      </div>
      <div class="spacer">
        <div class="divider"></div>
      </div>

    </div>
  </div>
</template>

<script>
import TextImage from '@/components/content/text-image';
import highlight from '@/directive/highlight';
import marked from 'marked';


export default {
  components: {
    TextImage,
  },
  directives: {
    highlight,
  },
  props: {
    content: {
      type: String,
    },
    intro: {
      type: String,
    },
  },
  data() {
    return {
      htmlStr: '',
    };
  },
  watch: {
    content(newVal) {
      this.UpdateContent(newVal);
    },
  },
  methods: {
    UpdateContent(data) {
      const renderer = new marked.Renderer();
      const option = {
        renderer,
        gfm: true,
        tables: true,
        breaks: true,
        pedantic: false,
        sanitize: false,
        smartLists: true,
        smartypants: false,
      };
      marked.setOptions(option);
      // const lexer = new marked.Lexer(option);
      // const tokens = lexer.lex(this.markedStr);
      // console.info(tokens);
      // const inlineLexer = new marked.InlineLexer(tokens.links, option);
      // console.info(tokens.links);
      // const data = inlineLexer.output('![test](https://xiak.com)');
      // console.info(data);
      this.htmlStr = marked.parse(data);
    },
  },
};
</script>

<style rel="stylesheet/scss" lang="scss">
  .work-content {
    padding-top: 0;
    background-color: #FFFFFF;
    border-radius: 3px 0 0;
  }
  .work-content-wrap {
    .work-modules {
      border-radius: 0;
      overflow: hidden;
    }
  }

  .module-spacer {
    height: 0;
    img {
      width: 100%;
    }
  }

  .p1 {
    font-size: 20px;
  }
  .p2 {
    font-size: 19px;
  }
  .p3 {
    font-size: 18px;
  }
  .p4 {
    font-size: 16px;
  }
  .p5 {
    font-size: 15px;
  }

  .align-center {
    text-align: center;
  }
  .work-module {
    padding: 0 5%;
    overflow: hidden;
    .main-text a {
      color: #1769FF;
      text-align: left;
      text-decoration: none;
    }
    &.align-center {
      margin-center: 0 !important;
    }
    &.full {
      padding-left: 0;
      padding-right: 0;
    }
  }

  .module {
    padding-bottom: 0px;
  }

  .module-spacer {
    height: 0;
    img {
      width: 100%;
    }
  }

  .spacer {
    height: 40px;
    &.border {
      height: 1px;
    }
    &.tiny {
      height: 10px;
    }
    &.mini {
      height: 20px;
    }
    &.medium {
      height: 30px;
    }
    &.large {
      height: 60px;
    }
  }

  .divider {
    display: none;
  }
  @media (max-width: 603px) {
    .p1 {
      font-size: 18px;
    }
    .p2 {
      font-size: 17px;
    }
    .p3 {
      font-size: 16px;
    }
    .p4 {
      font-size: 15px;
    }
    .p5 {
      font-size: 14px;
    }
  }
  @media (max-width: 1080px) and (min-width: 1024px) {
    .p1 {
      font-size: 20px;
    }
    .p2 {
      font-size: 19px;
    }
    .p3 {
      font-size: 18px;
    }
    .p4 {
      font-size: 17px;
    }
    .p5 {
      font-size: 16px;
    }
  }
  .markdown {
    font-size: 18px;
    font-weight: 400;
    line-height: 1.7;
    color: #262626;
    p {
      margin: 0 0 10px;
    }
    h1 {
      font-size: 30px;
      margin-bottom: 30px;
      text-align: center;
    }
    h2 {
      font-size: 22px;
      margin-bottom: 10px;
    }
    h3 {
      font-size: 16px;
      margin-bottom: 10px;
    }
    ol, ul {
      margin-top: 0;
      margin-bottom: 10px;
    }

    img {
      height: 100%;
      width: 100%;
      display: inline-block;
      max-width: 100%;
      vertical-align: middle;
    }

    code {
      font-size: 16px;
      font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', 'Consolas', 'source-code-pro', monospace;
      margin-bottom: 10px;
    }
  }
</style>



