import { fetchArticleDetail, fetchArticleSummary } from '@/api/article';

const article = {
  state: {
    content: '',
    summary: {},
  },
  mutations: {
    FETCH_FILE_SUMMARY: (state, data) => {
      state.summary = data;
    },
    FETCH_FILE_CONTENT: (state, data) => {
      state.content = data;
    },
  },
  actions: {
    fetchContent({ commit }, file) {
      fetchArticleDetail(file).then((response) => {
        const data = response.data;
        commit('FETCH_FILE_CONTENT', data);
      });
    },
    fetchSummary({ commit }) {
      fetchArticleSummary().then((response) => {
        const data = response.data;
        commit('FETCH_FILE_SUMMARY', data);
      });
    },
  },
};

export default article;



