import Vue from 'vue';
import Vuex from 'vuex';
import app from './app';
import article from './article';
import getters from './getters';

Vue.use(Vuex);

const store = new Vuex.Store({
  modules: {
    app,
    article,
  },
  getters,
});

export default store;



