import Vue from 'vue';
import Router from 'vue-router';
import Layout from '@/views/layouts';

Vue.use(Router);

/**
 * Loaded router on demand
 */
export const defaultRoutes = [
  {
    path: '/',
    component: Layout,
    redirect: '/article',
    name: 'Article',
    meta: {
      title: 'example',
      icon: 'example',
    },
  },
  {
    path: '/article',
    component: Layout,
    name: '文章',
    meta: { title: 'Article', noCache: true },
    redirect: '/article/kubernetes/Summary',
    children: [
      {
        path: 'kubernetes/:file*',
        component: () => import('@/components/content'),
      },
    ],
  },
];

export default new Router({
  mode: 'history',
  routes: defaultRoutes,
});



